html,
body {
  height: 100%;
}
h1 {
  font-size: 500%;
  color: #F4F4F9;
  font-family: 'Montserrat', sans-serif;
}

p {
  color: #B7DAD8;
  font-family: 'Montserrat', sans-serif;
  font-size: 150%;
}

#root {
  height: 100%;
}

.App {
  text-align: center;
  height: 100%;
}

.MainBanner {
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  /* background-color: #313D6B; */
  background-image: url("./imgs/bar.jpeg");
  background-size: cover;
}

.MainBannerItem, .feature {
  -ms-flex-positive: 1;
      flex-grow: 1;
}

.phonePic {
  width: 40%;
}

.titles { 
  display: -ms-flexbox; 
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-line-pack: start;
      align-content: flex-start;
}

.Features {
  height: 50%;
  background-color: #F4F4F9;
  display: -ms-flexbox;
  display: flex;  
  color: #000000;
  -ms-flex-align: center;
      align-items: center;  
}

.feature {
  width : 20%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.featureImage { 
  width : 25%;
}


.featureDescription { 
  font-size: 100%;
  color: #596E7B;
  width: 80%;
}


